import xllLoading from '@/components/xllLoading';
import { EVENTS_MY_EVENT, MY_DEALS, MY_JOBS, SET_USER_ADS, UPLOAD_BY_ALI_OSS, UPLOAD_BY_SERVICE } from "@/api/api";
import dashboardAdsImg from '@/assets/ads/2.png';
import meSideMenu from "@/components/meSideMenu";
import { decode } from 'js-base64';
import ImageCompressor from 'compressorjs';
export default {
  name: "redeem",
  components: {
    meSideMenu,
    xllLoading
  },

  data() {
    return {
      uploadLoadingStatus: false,
      dashboardAdsImg,
      uploadActionUrl: process.env.VUE_APP_UPLOAD_ACTION_URL,
      uploadHeaders: {
        platform: 4
      },
      uploadData: {
        token: localStorage.getItem('token')
      },
      identity: 0,
      jobPage: 1,
      jobLimit: 5,
      jobLastPage: 0,
      jobCurrentPage: 0,
      myApprovedJobsData: [],
      myDealsData: [],
      myEventsData: [],
      dealPage: 1,
      dealLimit: 100,
      dealLastPage: 0,
      eventPage: 1,
      eventLimit: 100,
      eventLastPage: 0,
      basicForm: {
        name: "",
        contact: '',
        wx_id: ''
      },
      basicRules: {
        name: [{
          required: true,
          message: 'Please fill out your name.',
          trigger: 'blur'
        }],
        contact: [{
          required: true,
          message: 'Please enter your contact',
          trigger: 'blur'
        }],
        wx_id: [{
          required: false,
          message: 'Please enter your wechat id',
          trigger: 'blur'
        }]
      },
      adsTabValue: 0,
      type: 0,
      typeId: 0,
      bannerImageUrl: '',
      adCategoryDetail: {}
    };
  },

  mounted() {
    let info = this.$route.query.info;
    let infoDecode = decode(info);
    this.adCategoryDetail = JSON.parse(infoDecode); // console.log(this.adCategoryDetail)

    let identity = localStorage.getItem('identity');
    this.identity = identity;

    if (identity == 2) {
      this.getJobListApproved(this.jobPage, this.jobLimit);
    }

    if (identity == 3) {
      this.getDealsList(this.dealPage, this.dealLimit);
      this.getEventsList(this.eventPage, this.eventLimit);
    }
  },

  methods: {
    turnBack() {
      this.$router.push('/me/ads');
    },

    bannerHttpRequest(options) {
      let self = this; // console.log(options)

      new ImageCompressor(options.file, {
        quality: 0.6,

        success(file) {
          // console.log(file)
          const formData = new FormData();
          formData.append('token', localStorage.getItem('token')); // console.log(file)

          let isInChina = process.env.VUE_APP_CHINA;

          if (isInChina === 'yes') {
            formData.append('file[]', file, file.name);
            UPLOAD_BY_ALI_OSS(formData).then(res => {
              // console.log(res)
              if (res.code == 200) {
                let myFileUrl = res.data[0]['file_url'];
                self.uploadLoadingStatus = false;
                self.bannerImageUrl = myFileUrl;
              }
            }).catch(err => {
              console.log(err);

              if (err.msg) {
                this.$message.error(err.msg);
              }

              if (err.message) {
                this.$message.error(err.message);
              }
            });
          }

          if (isInChina === 'no') {
            formData.append('file', file, file.name);
            UPLOAD_BY_SERVICE(formData).then(res => {
              // console.log(res)
              if (res.code == 200) {
                let myFileUrl = res.message.file_path;
                self.uploadLoadingStatus = false;
                self.bannerImageUrl = myFileUrl;
              }
            }).catch(err => {
              console.log(err);

              if (err.msg) {
                this.$message.error(err.msg);
              }

              if (err.message) {
                this.$message.error(err.message);
              }
            });
          }
        },

        error(err) {
          console.log(err.message);
        }

      });
    },

    beforeUpload(file) {
      this.uploadLoadingStatus = true;
      const isLt2M = file.size / 1024 / 1024 < 20;

      if (!isLt2M) {
        this.$message.error('Avatar picture size can not exceed 20MB');
      }

      return isLt2M;
    },

    cancelUpload() {
      this.uploadLoadingStatus = false;
    },

    // 
    getJobListApproved(page, limit) {
      let token = localStorage.getItem('token');
      let data = {
        token: token,
        page: page,
        limit: limit,
        status: 0
      };
      MY_JOBS(data).then(res => {
        // console.log(res)
        if (res.code == 200) {
          let jobData = res.message.data;
          this.jobLastPage = res.message.last_page;
          this.jobCurrentPage = res.message.current_page;
          this.myApprovedJobsData = jobData;
        } else {
          this.$message.error(res.msg);
        }
      }).catch(err => {
        console.log(err);

        if (err.msg) {
          this.$message.error(err.msg);
        }

        if (err.message) {
          this.$message.error(err.message);
        }
      });
    },

    getDealsList(page, limit) {
      let data = {
        token: localStorage.getItem('token'),
        page: page,
        limit: limit
      };
      MY_DEALS(data).then(res => {
        console.log(res);

        if (res.code == 200) {
          this.myDealsData = res.message.data;
        } else {
          this.$message.error(res.msg);
        }
      }).catch(err => {
        console.log(err);

        if (err.msg) {
          this.$message.error(err.msg);
        }

        if (err.message) {
          this.$message.error(err.message);
        }
      });
    },

    getEventsList(page, limit) {
      let data = {
        token: localStorage.getItem('token'),
        page: page,
        limit: limit
      };
      EVENTS_MY_EVENT(data).then(res => {
        console.log(res);

        if (res.code == 200) {
          this.myEventsData = res.message.data;
        } else {
          this.$message.error(res.msg);
        }
      }).catch(err => {
        console.log(err);

        if (err.msg) {
          this.$message.error(err.msg);
        }

        if (err.message) {
          this.$message.error(err.message);
        }
      });
    },

    submit() {
      let adCategoryDetail = this.adCategoryDetail;
      let type = this.adsTabValue;
      let typeId = this.typeId;
      let imageUrl = this.bannerImageUrl;
      let adCategoryId = adCategoryDetail.ad_category_id;
      let myAdsId = adCategoryDetail.id;

      if (imageUrl == '') {
        return this.$message.error('Please upload your banner');
      }

      let data = {
        ad_category_id: adCategoryId,
        my_ads_id: myAdsId,
        type: type,
        type_id: typeId,
        image_url: imageUrl,
        token: localStorage.getItem('token')
      };

      if (type == 8) {
        this.$refs['basicForm'].validate(valid => {
          if (valid) {
            data.name = this.basicForm.name;
            data.contact = this.basicForm.contact;
            data.wx_id = this.basicForm.wx_id;
          } else {
            console.log('error submit!!');
            this.submitLoginLoadingStatus = false;
            return false;
          }
        });
      }

      SET_USER_ADS(data).then(res => {
        console.log(res);

        if (res.code == 200) {
          this.$message.success(res.msg);
        } else {
          this.$message.error(res.msg);
        }
      }).catch(err => {
        console.log(err);

        if (err.msg) {
          this.$message.error(err.msg);
        }

        if (err.message) {
          this.$message.error(err.message);
        }
      });
    },

    adsTabEvent(value) {
      // var self = this;
      this.adsTabValue = value;
      this.typeId = 0;

      if (value == 2 || value == 3) {
        this.typeId = localStorage.getItem('uid');
      }

      if (value == 8) {
        console.log('8');
      }
    },

    selectLinkTypeId(typeId) {
      this.typeId = typeId;
    },

    prevJobs() {
      this.jobPage--;

      if (this.jobPage <= 1) {
        this.jobPage = 1;
      }

      this.getJobListApproved(this.jobPage, this.jobLimit);
    },

    nextJobs() {
      this.jobPage++;

      if (this.jobPage >= this.jobLastPage) {
        this.jobPage = this.jobLastPage;
      }

      this.getJobListApproved(this.jobPage, this.jobLimit);
    }

  }
};